import { JSX, Fragment } from 'preact';
import { cssProps } from '@oracle/oraclejet-cssprops';
import { Size, sizeToCSS } from '../utils/UNSAFE_size';
import { useTooltip } from '../hooks/UNSAFE_useTooltip';

type IntrinsicProps = JSX.SVGAttributes<SVGSVGElement>;

type Props = Pick<IntrinsicProps, 'viewBox' | 'children'> & {
  /**
   * The size of the component. Defaults to '1em', which means the font size of the parent is used.
   */
  size?: Size | '1em';
  /**
   * The color of the component. Defaults to 'currentColor', which behaves like the CSS 'currentColor' value.
   */
  color?: keyof typeof colorStyles;
  /**
   * A label to be used for accessibility purposes.
   */
  accessibleLabel?: string;
};

export const Icon = ({
  size = '1em',
  color = 'currentColor',
  accessibleLabel = '',
  viewBox = 'none',
  children
}: Props) => (
  <Svg
    height="1em"
    width="1em"
    viewBox={viewBox}
    accessibleLabel={accessibleLabel}
    class={colorStyles[color]}
    style={{ fontSize: size === '1em' ? size : `${sizeToCSS(size)}` }}>
    {children}
  </Svg>
);

type BaseSvgProps = Pick<
  IntrinsicProps,
  'height' | 'width' | 'viewBox' | 'class' | 'children' | 'style'
>;
type SvgProps = BaseSvgProps & {
  accessibleLabel: string;
};

const Svg = ({ accessibleLabel, ...otherProps }: SvgProps) =>
  accessibleLabel ? (
    <SemanticSvg accessibleLabel={accessibleLabel} {...otherProps} />
  ) : (
    <DecorativeSvg {...otherProps} />
  );

const DecorativeSvg = (props: BaseSvgProps) => <svg {...props} />;

const SemanticSvg = ({ accessibleLabel, children, ...props }: SvgProps) => {
  const { tooltipContent, tooltipProps } = useTooltip({
    text: accessibleLabel,
    anchor: {
      x: 'element',
      y: 'element'
    },
    position: 'bottom',
    offset: {
      mainAxis: 8, // redwood tooltip design spec
      crossAxis: 0
    }
  });

  return (
    <Fragment>
      <svg role="img" {...props} {...tooltipProps} aria-label={accessibleLabel}>
        {children}
      </svg>
      {tooltipContent}
    </Fragment>
  );
};

const colorStyles = {
  primary: cssProps`${{
    color: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-text-color-primary)'
  }}`,
  secondary: cssProps`${{
    color: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-text-color-secondary)'
  }}`,
  disabled: cssProps`${{
    color: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-text-color-disabled)'
  }}`,
  danger: cssProps`${{
    color: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-danger-3)'
  }}`,
  warning: cssProps`${{
    color: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-warning-3)'
  }}`,
  success: cssProps`${{
    color: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-success-3)'
  }}`,
  info: cssProps`${{
    color: 'var(--oj-c-PRIVATE-DO-NOT-USE-core-info-3)'
  }}`,
  currentColor: cssProps`${{
    color: 'currentColor'
  }}`
};
